					'isCollect': false,
					'labels': ['祝福', '红包', '对联']
				},
				{	
					'template_id': 1, 
					'design_img': require('@/assets/design/design_2.jpg'),
					'integral' : 10,
					'isCollect': false,
					'labels': ['利是', '红包', '对联']
				},
				{	
					'template_id': 1, 
					'design_img': require('@/assets/design/design_2.jpg'),
					'integral' : 10,
					'isCollect': false,
					'labels': ['利是', '红包', '对联']
				},
				{	
					'template_id': 1, 
					'design_img': require('@/assets/design/design_2.jpg'),
					'integral' : 10,
					'isCollect': false,
					'labels': ['灯笼', '红包', '对联']
				},
				{	
					'template_id': 1, 
					'design_img': require('@/assets/design/design_3.jpg'),
					'integral' : 10,
					'isCollect': false,
					'labels': ['祝福', '红包', '对联']
				},
				{	
					'template_id': 1, 
					'design_img': require('@/assets/design/design_2.jpg'),
					'integral' : 10,
					'isCollect': false,
					'labels': ['利是', '红包', '对联']
				},
			],
			design_labels: ['新年', '红包', '对联', '祝福', '灯笼', '利是']
		}
	},
	watch: {
		
	},
	computed: {
		download_path: function() {
			return this.filter_design_data[this.download_tips_index].design_img
		},
		filter_design_data: function() {
			// 过滤元素标签
			const _select_label_index= this.select_label_index;
			if(_select_label_index != -1) {
				const _select_label = this.design_labels[_select_label_index]
				const reg = new RegExp(_select_label,'ig')
				const _pushArr=[];
				this.origin_design_data.map(function(item) {
					item.labels.map((label) => {
						if(label.search(_select_label) != -1){
							_pushArr.push(item);
						}
					})
				})
				return _pushArr
			}
			return this.origin_design_data;
		}

	},
	mounted: function() {
		this.hoverFunc()
	},
	methods: {
		...mapMutations(['setAnswerIndex']),
		prevFunc: function() {
			const me =this;
			// 返回上一页
			me.$router.go(-1)
		},
		collectDesign: function(index, id) {
			const me =this;
			const tar =this.filter_design_data[index];
			tar.isCollect = !tar.isCollect
			if(!tar.isCollect) {
				// test
				me.cancel_collect_func(id)
			}else{
				me.to_collect_func(id)
			}
		},
		to_collect_func: function(template_id) {
			// 收藏
			const me = this;
			axios.post("http://dev.cyrd.gdinsight.com/webApi/Collection/add_collection",{
				template_id: template_id,
				is_user: me.need_render
			})
			.then(function(data) {
				console.log(data)
				me.$message('收藏成功！');
			})
			.catch(function(e) {
				console.log(e)
			})
		},
		cancel_collect_func: function(template_id) {
			// 取消收藏
			const me = this;
			axios.get(`http://dev.cyrd.gdinsight.com/webApi/Collection/change_collection?template_id=${template_id}&is_user=${me[`need_render`]}`)
			.then(function(data) {
				console.log(data)
				me.$message('取消成功');
			})
			.catch(function(e) {
				console.log(e)
			})
		},
		downsTips: function(index){
			this.download_tips_dia = true;
			this.download_tips_index = index
		},
		downsFunc: function() {

			this.downs(this.download_path, true);
			this.download_tips_dia = false
		},
		selectLabel: function(flag, index=-1){
			this.select_label_index = index;
		},
		hoverFunc: function() {
			let cards = document.querySelectorAll(".cards");
			let range = 15;
			let calcValue = function calcValue(a, b) {return (a / b * range - range / 2).toFixed(1);}; 
			let timeout = void 0;
			cards.forEach(function (card) {
				card.addEventListener('mousemove', function (_ref) {
					let x = _ref.x,
					y = _ref.y;
					if (timeout) {
						window.cancelAnimationFrame(timeout);
					}

					timeout = window.requestAnimationFrame(function () {
						let yValue = calcValue(y, window.innerHeight);
						let xValue = calcValue(x, window.innerWidth);
						// console.log(xValue, yValue);


						card.style.transform = "translateX(" + -xValue + "px) translateY(" + yValue + "px)";
					});
				}, false);

			})
		},
	},
	updated: function() {
		this.hoverFunc()
	},
	beforeRouteEnter(to, from, next) {
		next((vm) => {
			vm.hoverFunc()
		})
	},
}

</script>